.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  height: 100vh;
  /*
  background: repeat url("../images/circles-light.png");
  background-color: darkred;
  background-blend-mode: color-burn; */
  background: repeat url("../images/circles-light.png");
  cursor: -webkit-zoom-in;
  cursor: zoom-in;
}

.App-header {
  background: -webkit-radial-gradient(circle, orange, red, #4b0000);
  background: -o-radial-gradient(circle, orange, red, #4b0000);
  background: radial-gradient(circle, orange, red, #4b0000);
  height: 55px;
  padding: 10px;
  color: white;
  margin-bottom: .5em;
}

.App-title {
  margin: 0;
  font-family: codenamesFont;
  font-size: 2.5em;
  font-weight: 100;
}

@media screen and (max-width: 700px) {
  #debugTools {
    display: none !important;
  }
}



